.App{
  text-align: center;
  font-size: 21px;
  margin: 25vh auto;
  font-family: monospace;
}

.App-title{
  font-size: 36px;
  margin-bottom: 25px;
}

.clock-days,.clock-hours,.clock-minutes,.clock-seconds{
  display: inline;
  margin: 0 15px;
}

input{
  margin: 25px auto;
}

.Deadline-input{
  font-size: 21px;
}

Button{
  background-color: #f04 !important;
  color:#fff !important;
}

div img{
  width: 100px;
}

@media only screen and (max-width:360px){
  .App-title{
    font-size:21px;
    padding: 1px;
  }
  .App{
    margin: 15vh auto;
    font-size: 16px;
  }
  .clock-days,.clock-hours,{
    margin: 0 15px;
  }
  .clock-minutes,.clock-seconds{
    display: inline-block;
  }
  input{
    margin-bottom: 10px;
  }
}



